.snackbar__container {
  display: flex;
  position: fixed;
  top: 15px;
  right: 15px;
  background: #e95e5e;
  margin: 0 auto;
  -webkit-animation: woot 0.6s;
  animation: woot 0.6s;
  z-index: 99;
}
.snackbar__dismiss,
.snackbar__message {
  text-align: center;
  color: #fff;
  padding: 0.2em;
  border-radius: 5px;
}
.snackbar__message {
  display: block;
  flex: 1 0 85%;
  font-weight: 500;
  font-size: 14px;
}
.snackbar__dismiss {
  display: block;
  flex: 0 1 15%;
  font-weight: 300;
  font-size: 12px;
  color: #fff;
}
@-webkit-keyframes woot {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1%;
  }
}
@keyframes woot {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1%;
  }
}
