.blink {
  -webkit-animation: blink 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation: blink 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
@-webkit-keyframes blink {
  0% {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
  }
  25% {
    -webkit-filter: brightness(80%);
    filter: brightness(80%);
  }
  50% {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
  }
  75% {
    -webkit-filter: brightness(80%);
    filter: brightness(80%);
  }
  to {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
  }
}
@keyframes blink {
  0% {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
  }
  25% {
    -webkit-filter: brightness(80%);
    filter: brightness(80%);
  }
  50% {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
  }
  75% {
    -webkit-filter: brightness(80%);
    filter: brightness(80%);
  }
  to {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
  }
}
