.slide-up-appear,
.slide-up-enter {
  transform: translateY(100%);
  opacity: 0;
}
.slide-up-appear-active,
.slide-up-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  transition: transform var(--animation_duration) ease-in-out,
    opacity var(--animation_duration) ease-in-out;
}
.slide-up-exit {
  transform: translateY(0);
  opacity: 1;
}
.slide-up-exit-active {
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  transition: transform var(--animation_duration) ease-in-out,
    opacity var(--animation_duration) ease-in-out;
}
.slide-down-appear,
.slide-down-enter,
.slide-up-exit-active {
  transform: translateY(-100%);
  opacity: 0;
}
.slide-down-appear-active,
.slide-down-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  transition: transform var(--animation_duration) ease-in-out,
    opacity var(--animation_duration) ease-in-out;
}
.slide-down-exit {
  transform: translateY(0);
  opacity: 1;
}
.slide-down-exit-active {
  transform: translateY(100%);
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  transition: transform var(--animation_duration) ease-in-out,
    opacity var(--animation_duration) ease-in-out;
  opacity: 0;
}
.error-vibrate {
  -webkit-animation: vibrate 0.7s linear 1;
  animation: vibrate 0.7s linear 1;
}
.error-display {
  color: tomato;
  opacity: 0;
  transition: opacity 0.5s linear;
}
.error-display.show {
  opacity: 1;
}
@-webkit-keyframes vibrate {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(10px);
  }
  50% {
    transform: translateX(-10px);
  }
  75% {
    transform: translateX(10px);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes vibrate {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(10px);
  }
  50% {
    transform: translateX(-10px);
  }
  75% {
    transform: translateX(10px);
  }
  to {
    transform: translateX(0);
  }
}
